#drinkList {
    margin-bottom: 40px;
    background-color: whitesmoke;
}

.drinkContainer {
    display: flex;
    flex-wrap: nowrap;
    flex-direction: row;
    justify-content: space-between;

    background-color: rgb(119, 187, 99);
    padding: 15px;
    margin: 15px auto;
    border-radius: 5px;
    overflow: auto;
    width: 860px;
}

.drinkChildDiv {
    height: 192px;
    margin: 0px 10px;
}

.drinkImg {
    float:left;
    width: auto;
}

.drinkTable {
    flex-grow: 2;
    background-color: whitesmoke;
    border-radius: 5px;
}

.drinkDescription {
    align-self: flex-end;
    flex-grow: 0;

    float:right;
    background-color: whitesmoke;
    border-radius: 5px;
    width: 20%;
    text-align: center;
}